.header {
  height: 80px;
  background-color: white;
}
.header .header-top {
  height: 40px;
  text-align: right;
  line-height: 40px;
  padding-right: 30px;
  border-bottom: 1px solid #1da57a;
}
.header .header-top span {
  margin-right: 10px;
}
.header .header-bottom {
  display: flex;
  align-items: center;
  height: 40px;
}
.header .header-bottom .header-bottom-left {
  width: 25%;
  text-align: center;
  font-size: 20px;
}
.header .header-bottom .header-bottom-right {
  width: 75%;
  text-align: right;
  padding-right: 30px;
}
.header .header-bottom .header-bottom-right img {
  margin: 0 5px 0 15px;
  width: 30px;
  height: 30px;
}
.header .header-bottom .header-bottom-right .link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0;
}
.header .header-bottom .header-bottom-right .link-button:hover,
.header .header-bottom .header-bottom-right .link-button:focus {
  text-decoration: none;
}
