.login {
  background-image: url(/static/media/bg.ab45504b.jpg);
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
}
.login .login-header {
  align-items: center;
  display: flex;
  height: 120px;
  width: 100%;
  align-content: center;
  background-color: rgba(21, 20, 13, 0.2);
}
.login .login-header img {
  width: 60px;
  height: 60px;
  margin: 0 30px 0 70px;
}
.login .login-header h1 {
  font-size: 40px;
  color: white;
}
.login .login-content {
  width: 400px;
  height: 300px;
  margin: 100px auto;
  background-color: white;
  text-align: center;
  padding: 30px 50px;
}
.login .login-content h2 {
  color: black;
  font-weight: bold;
  font-size: 30px;
  margin-bottom: 20px;
}
.login .login-content Button {
  margin-top: 20px;
  width: 100%;
}
